import { useState } from 'react';
import './css/App.css';
import Main from "./js/modules/main.js"
import MainList from "./js/modules/MainList.js"


export default function App() {

  const [tela, setTela] = useState("main") 
  
  if(tela === "main"){
    return <Main setTela={setTela}></Main>
  }else if(tela === "MainList"){
    return <MainList></MainList>
  }
  
}


