import { useEffect, useState } from "react"
import "../../css/App.css"
import RenderDataWithProp from "./renderDataWithProp"
import {getRender, renders} from "../functions/requestAddress.js"


export default function MainList(props){
    
    const [render, setRender] = useState([])

    useEffect(()=>{
        getRender(setRender)
    },[])


    if(render === []){
        return(
            <div style={{display:"flex", alignItems:"center", justifyContent:"center"}}>
                <h1>Espera que tá Carregando</h1>
            </div>
        )
    }else{
            return(    
                <div className="main">
                    <header className="header">
                        <h1>NFTSpace</h1>
                    </header>
                    <div className="subHeader">NFT DATA: </div>
                    <div>
                        {
                            render.map((item)=> {return item} )
                        }
                    </div>
                </div>
            )
            }
}