import RenderDataWithProp from "../modules/renderDataWithProp"
import requestBlock from "./request"

export var renders = []

function mostraRender(setRender){
    setRender([...renders])
}


export async function getRender(setRenders){

    var addressBlock
    try {
        addressBlock = await requestAddressBlock()
    } catch (error) {   
    }

    console.log(addressBlock)

    var i = 0

    while(i < addressBlock.length){
        let object = await requestBlock(addressBlock[i])

        if(typeof object != "undefined"){
            object = <RenderDataWithProp objeto={object}></RenderDataWithProp>
        }

        if(!renders.includes(object) && typeof object != "undefined"){
            renders.push(object)
        }

        mostraRender(setRenders)
        console.log([...renders])
        i++

    }
 }




async function requestAddressBlock(){
    const address = "1GHz8oqDXRRSBiXfWxkmB3fzCFvLaCbAMF"
    let addressBlock = await requestAddressJSONBlock(address)
    
    addressBlock = trataAddressJson(addressBlock)
    
    return addressBlock

}

async function requestAddressJSONBlock(address){

    let url = `https://api.whatsonchain.com/v1/bsv/main/address/${address}/history`
    try {
        let res = await fetch(url);
        return await res.json()
    } catch (error) {
        
    } 
}

function trataAddressJson(addressBlock){
    let txIDS = []

    addressBlock.map((address)=>{
        txIDS.push(address.tx_hash)
    })

    return txIDS
}

