import { useState } from "react";
import "../../css/App.css"
import Renderdata from "./renderData.js"

export default function Main(props){

    const setTela = props.setTela

    const [footerContent, setFooterContent] = useState({
        "Token":"",
        "TX1":"",
        "ADD":"",
        "TXID":""
      })

    return (
        <div className="main">
          <header className="header">
            <h1>NFTSpace</h1>
            <input type="button" 
            onClick={()=>setTela("MainList")} 
            style={{marginRight:50}}
            value="NFT List"></input>
          </header>
          <div className="subHeader">NFT DATA: </div>
          <div className="conteudo">
            <Renderdata 
            setFooterContent={setFooterContent}
            ></Renderdata>
          </div>
          <footer>
            <h1>TOKEN: {footerContent.Token}</h1>
            <h1>TX1: <a href={footerContent.TXID}>{footerContent.TX1}</a></h1>
            <h1>ADD: {footerContent.ADD}</h1>
          </footer>
        </div>  
      );
}