import React, { useState } from 'react';
import '../../css/App.css';
import requestBlock from "../functions/request.js"

export default function Renderdata(props){

    const [txID, setTXID] = useState('')
    const [modal, setModal] = useState(true)
    const [conteudo, setConteudo] = useState(null)

    async function mostraConteudo(){
        try {
            var conteudoJSON = await requestBlock(txID)
        } catch (error) {

        }  
       
        props.setFooterContent({
            "Token":conteudoJSON.Token,
            "TX1":conteudoJSON.TX1,
            "ADD":conteudoJSON.ADD,
            "TXID":conteudoJSON.TXID
          })

       
        await setConteudo(conteudoJSON)
        setModal(false)

    }


    if(modal){
        return(
            <div className="modal">
                <input type="text" id="inputTXID" onChange={(event)=>setTXID(event.target.value)}></input>
                <button onClick={mostraConteudo} id="submit">Enviar</button>
            </div>
        )
    }else{
        return(
            <div className="imagemNFT" style={{display:'flex',flexDirection:'column'}}>
                {
                    (conteudo.isImage)
                    ?
                    <img src={conteudo.Content} className="imagemNFT" alt="imagem"></img>
                    :
                    <p>{conteudo.Content}</p>
                }
                <input type="button" value="Digite outro NFT" 
                onClick={() => {setModal(true); props.setFooterContent({
                    "Token":"",
                    "TX1":"",
                    "ADD":""
                  })}}></input>
            </div>
        )
    }

}


