import React, { useEffect, useState } from 'react';
import '../../css/App.css';

export default function RenderDataWithProp(props){

    const [conteudo, setConteudo] = useState(props.objeto)


   if(conteudo.Content !== {}){
    return(
        <div className="renderDataWithProp">
            <div className="renderDataWithProp-left">
                <h1>MainID: <a>{conteudo.MainID}</a></h1>
            </div>
            <div className="renderDataWithProp-right">
                {
                (conteudo.isImage)
                ?
                    <img  
                    src={conteudo.Content} 
                    className="imagemAddressPreview"
                    alt="Descrição da Imagem"></img>
                :
                <p>{conteudo.Content}</p>
                }
            </div>
        </div>
    )
   }
}

